@import '../../index.scss';
.modal{
  width: 100vw;
  height: 100vh;
  background-color: rgba($color: #000000, $alpha: .6);
  position: fixed;
  left: 0;
  top: 0;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  opacity: 0;
  pointer-events: none;
}
.modal.active{
  opacity: 1;
  pointer-events: all;
}
.modal__content{
  border-radius: 10px;
  transition: 0.3s all;
  transform:  scale(0.1);
  width: 60%;
  @media (max-width: 425px){
    width:85%;
  }
}
.modal__content.active{
  transform:  scale(1);
}